import { PageHero } from "@/components/ui/page-hero"
import { LeadForm } from "@/components/forms/lead-form"
import { Button } from "@/components/ui/button"
import { WhyChooseInsura } from "@/components/content/why-choose-insura"
import { Check, Shield, Home, FileText, AlertTriangle } from "lucide-react"
import { FAQ } from "@/components/content/faq"
import { Truck } from "lucide-react"

const coverageTypes = [
  { title: "Comprehensive Coverage", description: "Full protection for your commercial vehicles" },
  { title: "Third-Party Liability", description: "Mandatory coverage for business vehicles" },
  { title: "Goods in Transit", description: "Protection for cargo and goods being transported" },
  { title: "Driver Liability", description: "Coverage for employee drivers" },
]

const faqs = [
  {
    question: "What is commercial vehicle insurance?",
    answer:
      "Commercial vehicle insurance provides coverage for vehicles used primarily for business purposes, including delivery vans, trucks, and company cars.",
  },
  {
    question: "What vehicles are covered?",
    answer:
      "Coverage includes trucks, vans, pickup trucks, buses, taxis, and any vehicle registered for commercial use.",
  },
  {
    question: "Can I cover multiple commercial vehicles?",
    answer:
      "Yes, we offer fleet insurance options for businesses with multiple commercial vehicles under a single policy.",
  },
]

export default function CommercialInsurancePage() {
  return (
    <main>
      {/* HERO BANNER */}
      <section className="relative overflow-hidden bg-primary min-h-[40vh]">
        <div className="absolute inset-0">
          <img
            src="/car3.jpg"
            alt="Bike Insurance"
            className="w-full h-full object-center"
          />
          <div className="absolute inset-0 bg-gradient-to-r from-primary via-secondary/90 to-transparent" />
        </div>

        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-32">
          <div className="max-w-2xl text-white">
            <div className="flex items-center gap-2 text-white/80 mb-4 text-sm">
              <span>insura.ae</span>
              <span className="w-1.5 h-1.5 bg-white rounded-full" />
              <span>Motor Insurance</span>
            </div>

            <h1 className="text-4xl md:text-5xl font-bold mb-4">
              Commercial Vehicle Insurance
            </h1>

            <p className="text-lg text-white/90">
              Need any type of insurance policies over the world you can contact us without any hesitation. Our expert support team with help you.
            </p>
          </div>
        </div>
      </section>

      <section className="py-16 bg-background">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">

          <div className="mb-8">
            <h2 className="text-3xl md:text-4xl font-bold text-primary leading-snug">
              Commercial Vehicle Insurance to Ease Your Business
            </h2>
            <h2 className="text-3xl md:text-3xl text-gray-500 leading-snug mb-10 mt-5">
              <span className="font-bold text-gray-500">Commercial Vehicle Insurance</span> is a specialized insurance policy designed to cover vehicles used for business purposes in UAE
            </h2>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
            <div className="lg:col-span-2 space-y-12">

              <div className="mb-8">
            <h2 className="text-3xl md:text-4xl font-bold text-primary leading-snug">Buy Insurance That Fits You....</h2>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {coverageTypes.map((type, index) => (
                  <div key={index} className="perspective group">
                    <div
                      className="
                        relative
                        h-56
                        w-full
                        transition-transform
                        duration-700
                        transform-style-3d
                        group-hover:rotate-y-180
                      "
                    >
                      {/* FRONT */}
                      <div
                        className="
                          absolute inset-0
                          bg-primary
                          flex flex-col
                          items-center
                          justify-center
                          text-white
                          backface-hidden
                        "
                      >
                        <Home className="w-10 h-10 text-white-400 mb-4" />
                        <h3 className="text-lg font-semibold text-center px-4">
                          {type.title}
                        </h3>
                      </div>

                      {/* BACK */}
                      <div
                        className="
                          absolute inset-0
                          bg-white
                          flex flex-col
                          items-center
                          justify-center
                          text-center
                          px-6
                          border
                          backface-hidden
                          rotate-y-180
                        "
                      >
                        <p className="text-muted-foreground mb-4">
                {type.description}
              </p>

                        <Button className="bg-primary px-6 py-2">
                          Buy Now
                        </Button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              <div className="bg-accent/30 rounded-xl p-8">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                  <div>
                    <h2 className="text-2xl font-bold text-foreground mb-4">Get Commercial Insurance Quote</h2>
                    <p className="text-muted-foreground mb-6">
                      Protect your business vehicles with tailored commercial coverage solutions.
                    </p>
                  </div>
                  <LeadForm type="business" title="Get Quote" />
                </div>
              </div>

              <div>
                <h2 className="text-2xl font-bold text-foreground mb-4">What is Commercial Vehicle Insurance?</h2>
                <p className="text-muted-foreground mb-4">
                  Commercial vehicle insurance provides protection for vehicles used for business purposes. Whether you
                  operate delivery trucks, company cars, or a fleet of service vehicles, this coverage ensures your
                  business operations are protected.
                </p>
              </div>

            </div>

            <div>
              <Sidebar />
            </div>
          </div>
        </div>
      </section>

      <WhyChooseInsura />
      <FAQ items={faqs} />
    </main>
  )
}
